export default {
    tooltip: {
        show: true,
        trigger: "item",
        triggerOn: "mousemove",
        position: 'top',
        formatter: function (param) {
            return param.data.name;
        }
    },
    series: [{
        type: "tree",
        data: [],
        left: "5%",
        right: "5%",
        top: "20%",
        bottom: "20%",
        symbol: "emptyCircle",
        orient: "vertical",
        expandAndCollapse: false,
        symbolSize: 15,
        label: {
            normal: {
                position: "top",
                rotate: 0,
                align: "middle",
                fontSize: 14,
                backgroundColor: "#62a7fd",
                padding: 10,
                borderRadius: 5,
                color: "#fff"
            }
        },
        leaves: {
            label: {
                normal: {
                    position: "bottom",
                    rotate: 0,
                    align: "middle",
                    fontSize: 16,
                },
            }
        },
        animationDurationUpdate: 750
    }]
}