<template>
    <el-dialog class="house-info-view-dialog" title="详细信息" :visible.sync="dialogVisible" @close="resetForm" fullscreen append-to-body>
        <div class="top">
            <techo-card class="base-house">
                <div slot="header">{{ title }}</div>
                <el-row class="base-house-item">
                    <el-col class="item-title" :span="6">户号：</el-col>
                    <el-col :span="18">{{ houseInfo.houseNum }}</el-col>
                </el-row>
                <el-row class="base-house-item">
                    <el-col class="item-title" :span="6">户主姓名：</el-col>
                    <el-col :span="18">{{ houseInfo.householderName }}</el-col>
                </el-row>
                <el-row class="base-house-item">
                    <el-col class="item-title" :span="6">户主电话：</el-col>
                    <el-col :span="18">{{ houseInfo.householderPhone }}</el-col>
                </el-row>
                <el-row class="base-house-item">
                    <el-col class="item-title" :span="6">户口类型：</el-col>
                    <el-col :span="18">{{ houseInfo.dicMap && houseInfo.dicMap.type }}</el-col>
                </el-row>
                <el-row class="base-house-item">
                    <el-col class="item-title" :span="6">单元楼层：</el-col>
                    <el-col :span="18">{{ houseInfo.name }}</el-col>
                </el-row>
            </techo-card>
            <techo-card class="house-people" resizeEvent="peopleCard">
                <div slot="header">与户主关系图</div>
                <div id="HousePeople" style="height:100%"></div>
            </techo-card>
        </div>
        <div class="bottom">
            <techo-card class="house-other" @onUpLevel="handleUpBack" :upLevel="isShowBack">
                <div slot="header">{{ otherTitle.detailsTitle }}</div>
                <div v-show="isShowList == false" id="houseContainer"></div>
                <div v-show="isShowList == true" class="house-org">
                    <el-table ref="tableList" size="medium" :data="tableData" border highlight-current-row tooltip-effect="dark"
                        stripe height="100%" style="width: 100%"
                        header-cell-class-name="techo-list__body-table-header">
                        <el-table-column type="index" label="序号" width="50" align="center"></el-table-column>
                        <el-table-column v-for="(value, key, index) in handerList" :key="index" :label="key" :prop="value" align="center" show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column label="操作" fixed="right" align="center" width="100">
                            <template slot-scope="scope">
                                <el-button size="mini" class="techo-list__body-table-button-view" @click.stop="handleDetailsInfo(scope.row, otherTitle)">查看</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </techo-card>
            <techo-card class="house-people-info">
                <div slot="header">人员信息</div>
                <div class="base-info">
                    <el-row :gutter="10">
                        <el-col :span="6" style="text-align: center;">
                            <img src="./images/photo.jpg" style="height: 120px;width: 110px;">
                        </el-col>
                        <el-col :span="18">
                            <div class="tacho-people-item">
                                <span class="item-title">姓名：</span>
                                <span class="item-value">{{ peopleInfo.name }}</span>
                            </div>
                            <div class="tacho-people-item">
                                <span class="item-title">身份证号：</span>
                                <span class="item-value">{{ peopleInfo.idCardNo }}</span>
                            </div>
                            <div class="tacho-people-item">
                                <span class="item-title">出生日期：</span>
                                <span class="item-value">{{ peopleInfo.birthDate }}</span>
                            </div>
                            <div class="tacho-people-item">
                                <span class="item-title">性别：</span>
                                <span class="item-value">{{ peopleInfo.dicMap && peopleInfo.dicMap.sex }}</span>
                            </div>
                            <div class="tacho-people-item">
                                <span class="item-title">籍贯：</span>
                                <span class="item-value">{{ peopleInfo.dicMap && peopleInfo.dicMap.nativePlace }}</span>
                            </div>
                            <div class="tacho-people-item">
                                <span class="item-title">民族：</span>
                                <span class="item-value">{{ peopleInfo.dicMap && peopleInfo.dicMap.nation }}</span>
                            </div>
                            <div class="tacho-people-item">
                                <span class="item-title">学历：</span>
                                <span class="item-value">{{ peopleInfo.dicMap && peopleInfo.dicMap.education }}</span>
                            </div>
                            <div class="tacho-people-item">
                                <span class="item-title">婚姻状况：</span>
                                <span class="item-value">{{ peopleInfo.dicMap && peopleInfo.dicMap.marriage }}</span>
                            </div>
                            <div class="tacho-people-item">
                                <span class="item-title">政治面貌：</span>
                                <span class="item-value">{{ peopleInfo.dicMap && peopleInfo.dicMap.politicalStatus }}</span>
                            </div>
                            <div class="tacho-people-item">
                                <span class="item-title">宗教信仰：</span>
                                <span class="item-value">{{ peopleInfo.dicMap && peopleInfo.dicMap.religiousBelief }}</span>
                            </div>
                        </el-col>
                    </el-row>
                    <div class="other-info" @click="handleDetailsInfo">更多信息>></div>
                </div>
                <div class="relevance-info">
                    <div id="KeyPeople" style="height:100%"></div>
                </div>
            </techo-card>
        </div>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'" :title="detailsTitle"></techo-details>
    </el-dialog>
</template>

<script>
import treeOptions from "./config/treeOptions.js";
import graphConfig from "./config/graphOptions.js";
import BallBox from "./scripts/collision.min.js";
import config from "./config/houseOther.js";
import service from "./services/build_info_service.js";
export default {
  props: {
    title: {
      type: String,
      default: "标题"
    }
  },
  data() {
    return {
      dialogVisible: false,
      houseInfo: {},
      // 与户主关系图
      treeOptions: {},
      treeChart: null,

      // 重点人群关系图
      keyPeopleOptions: {},
      keyPeopleChart: null,
      peopleInfo: {}, // 人员信息

      // 详情页面是否显示
      dlgVisible: false,
      configKey: "housePeople",

      // 是否显示企业 场所 等列表
      isShowList: false,
      tableData: [],
      handerList: [],
      otherTitle: { detailsTitle: "其他信息", configKey: "" },

      SAYS: _.cloneDeep(config),

      isShowBack: false,

      detailsTitle: "",
    };
  },

  watch: {
    dialogVisible(value) {
      if (value == true) {
        this.$nextTick(() => {
          // 家庭树关系图
          this.treeChart = this.$echarts.init(
            this.$el.querySelector("#HousePeople")
          );
          this.treeOptions = _.cloneDeep(treeOptions);
          // 重点人关系图
          this.keyPeopleChart = this.$echarts.init(
            this.$el.querySelector("#KeyPeople")
          );
          // 查询当前户下包含的人信息
          service.requsetHousePeopleList(this.houseInfo.id, data => {
            let dataList = _.groupBy(data.list, "dicMap.householdRelation");
            if(dataList["本人或户主"] == undefined) return false;
            let optionData = [
              {
                name: "户主：" + dataList["本人或户主"][0].name,
                value: dataList["本人或户主"][0],
                children: []
              }
            ];
            this.peopleInfo = dataList["本人或户主"][0];
            this.getFocusPeople();
            delete dataList["本人或户主"];
            _.map(dataList, (values, key) => {
              optionData[0].children.push({
                name: key,
                value: false,
                children: _.map(values, item => {
                  return { name: item.name, value: item };
                })
              });
            });
            this.treeOptions.series[0].data = optionData;
            this.treeChart.setOption(this.treeOptions);
            this.treeChart.resize();
          });
          // 生成碰撞泡泡
          this.buildPopupItem();
          // 绑定点击事件
          this.bindClickEvent();
        });
      }
    }
  },

  mounted() {
    let that = this;
    this.$bus.$on("peopleCard", () => {
      that.treeChart.resize();
      that.keyPeopleChart.resize();
    });
  },
  methods: {
    resetForm() {
      $("#houseContainer").empty();
      // 与户主关系图
      this.treeOptions = {};
      this.treeChart.dispose();
      this.treeChart = null;

      // 重点人群关系图
      this.keyPeopleOptions = {};
      this.keyPeopleChart.dispose();
      this.keyPeopleChart = null;

      this.houseInfo = {};
      this.peopleInfo = {}; // 人员信息

      // 详情页面是否显示
      this.dlgVisible = false;
      this.configKey = "housePeople";

      // 是否显示企业 场所 等列表
      this.isShowList = false;
      this.tableData = [];
      this.handerList = [];
      this.otherTitle = { detailsTitle: "其他信息", configKey: "" };
      this.isShowBack = false;

      this.detailsTitle = "";
    },
    handleUpBack() {
      this.isShowBack = false;
      this.isShowList = false;
      this.tableData = [];
      this.handerList = [];
      this.otherTitle = { detailsTitle: "其他信息", configKey: "" };
    },
    rand(m, n) {
      return m + parseInt((n - m) * Math.random());
    },
    buildPopupItem() {
      let that = this;
      let oC = document.getElementById("houseContainer");
      let mxwidth = oC.offsetWidth;
      let mxheight = oC.offsetHeight;
      let oB = new BallBox.BallBox("houseContainer");

      oB.ballRun();
      _.map(this.SAYS, (item, index) => {
        let b = that.rand(60, 80);
        let x = that.rand(b, mxwidth - b);
        let y = that.rand(b, mxheight - b);
        let ball = new BallBox.Ball({
          b: b,
          x: x,
          y: y,
          sx: that.rand(1, 4),
          sy: that.rand(1, 4),
          c: "url(" + require("./images/img/paopao" + index + ".png") + ")",
          html: item.name + "：" + that.houseInfo[item.count],
          fontSize: 18,
          clickCallBack: function() {
            // 查询当前户下包含的经济企业、社会组织、出租房、学校等信息
            service.requestOrgList(
              {
                districtCode: that.houseInfo.districtCode,
                houseId: that.houseInfo.id,
                url: item.url
              },
              data => {
                if (data.totalCount == 0) {
                  that.isShowBack = false;
                  that.isShowList = false;
                  that.tableData = [];
                  that.handerList = [];
                  that.otherTitle = { detailsTitle: "其他信息", configKey: "" };
                } else {
                  that.isShowBack = true;
                  that.isShowList = true;
                  that.tableData = data.list;
                  that.handerList = item.hander;
                  that.otherTitle = { detailsTitle: item.title, configKey: item.configKey };
                }
              }
            );
          }
        });
        oB.addBall(ball);
      });
    },
    bindClickEvent() {
      // 家庭人员关系树点击
      this.treeChart.on("click", item => {
        if (item.value != false) {
          this.peopleInfo = item.value;
          this.getFocusPeople();
        }
      });
      // 综治重点人关系点击
      this.keyPeopleChart.on("click", val => {
        if (val.value != undefined) {
          // 获取关注人人员信息
          service.requestFocus(val.value, data => {
            // 重点青少年
            data.iscriminal != undefined &&
              (data.dicMap.iscriminal = data.iscriminal == 1 ? "有" : "无");
            // 校园重点人
            data.isFocus != undefined &&
              (data.dicMap.isFocus = data.isFocus == 1 ? "是" : "否");
            // 流动人口
            data.isfocus != undefined &&
              (data.dicMap.isfocus = data.isfocus == 1 ? "是" : "否");
            // 社区矫正
            data.isrecidivist != undefined &&
              (data.dicMap.isrecidivist = data.isrecidivist == 1 ? "是" : "否");
            data.isgroup != undefined &&
              (data.dicMap.isgroup = data.isgroup == 1 ? "是" : "否");
            data.ishosted != undefined &&
              (data.dicMap.ishosted = data.ishosted == 1 ? "是" : "否");
            data.isdraintube != undefined &&
              (data.dicMap.isdraintube = data.isdraintube == 1 ? "是" : "否");
            data.iscrimeagin != undefined &&
              (data.dicMap.iscrimeagin = data.iscrimeagin == 1 ? "是" : "否");
            // 吸毒人员
            data.isCriminal != undefined &&
              (data.dicMap.isCriminal = data.isCriminal == 1 ? "有" : "无");
            // 精神病
            data.isSecurity != undefined &&
              (data.dicMap.isSecurity = data.isSecurity == 1 ? "是" : "否");
            data.isaccident != undefined &&
              (data.dicMap.isaccident = data.isaccident == 1 ? "有" : "无");
            // 刑满释放
            data.isRecidivism != undefined &&
              (data.dicMap.isRecidivism = data.isRecidivism == 1 ? "是" : "否");
            data.crimeAgain != undefined &&
              (data.dicMap.crimeAgain = data.crimeAgain == 1 ? "是" : "否");
            this.handleDetailsInfo(data, val.value);
          });
        }
      });
    },
    /** 查看人员详细信息 */
    handleDetailsInfo(data, val) {
      let newData = data.id == undefined ? _.cloneDeep(this.peopleInfo) : data;
      this.configKey = val == undefined ? "housePeople" : val.configKey;
      this.detailsTitle = val == undefined ? "户籍人口" : val.detailsTitle;

      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = newData;
        this.$refs["details"].visible = true;
      });
    },
    /** 查询重点人综治信息 */
    getFocusPeople() {
      let that = this;
      let keyValue = _.cloneDeep(graphConfig.keyValue);
      this.keyPeopleOptions = _.cloneDeep(graphConfig.options);
      service.requestFocusPeople(this.peopleInfo.peopleId, data => {
        that.keyPeopleOptions.series[0].data[0].name = that.peopleInfo.name;
        delete data.basePeopleId;
        delete data.housePeopleId;
        _.map(data, (item, index) => {
          if (item != null) {
            that.keyPeopleOptions.series[0].data.push({
              name: keyValue[index].name,
              value: {
                focusId: item,
                entity: keyValue[index].entity,
                configKey: keyValue[index].configKey,
                detailsTitle: keyValue[index].name,
              },
              draggable: true,
              symbolSize: 80,
              itemStyle: {
                normal: {
                  color: keyValue[index].color
                }
              }
            });
          } else {
            that.keyPeopleOptions.series[0].data.push({
              name: keyValue[index].name,
              draggable: true,
              symbolSize: 80,
              itemStyle: {
                normal: {
                  color: "#c7c7c7"
                }
              }
            });
          }
          that.keyPeopleChart.setOption(that.keyPeopleOptions);
          that.keyPeopleChart.resize();
        });
      });
    }
  }
};
</script>

<style lang="scss">
@import "./styles/house-info-view.scss";
</style>


